import {SocketService} from '../socket.service';
import {BehaviorSubject, combineLatest, interval, Subject} from 'rxjs';
import {Feature, Update, WindowResult, OverwolfWindow} from './overwolf.interfaces';
import {CreationRequest, SocketEvents} from '../socket.interface';
import {takeUntil} from 'rxjs/operators';

declare const overwolf; // Overwolf uses a build in js file

const overwolfEvents = overwolf.games.events;

export class OverwolfService {

  socketService: SocketService = new SocketService();
  MainWindow: OverwolfWindow;

  summonerId$: Subject<number> = new Subject<number>();
  region$: Subject<string> = new Subject<string>();
  matchState$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

  matchPrep$ = combineLatest([this.matchState$, this.summonerId$, this.region$]);

  private usingFeatures: Feature[] = [
    Feature.matchState,
    Feature.summonerInfo,
    Feature.gameMode
  ];

  constructor() {
    this.setFeatures();

    // Starts the match when all the information is ready
    this.matchPrep$
      .subscribe((results: any[]) => {

          const isStarted: boolean = results[0];
          const summonerId: number = results[1];
          const region: string = results[2];

          if (isStarted) {
            this.startMatch(region, summonerId);
          }
        }
      );

    // Gets the main window and saves it for later use
    overwolf.windows.getCurrentWindow((result: WindowResult) => {
      if (result.status === 'success') {
        this.MainWindow = result.window;
      }
    });

    // Check if match is already started
    overwolfEvents.getInfo((info) => {
      if (info.res.summoner_info.id) {
        this.summonerId$.next(info.res.summoner_info.id);
        this.region$.next(info.res.summoner_info.region);

        this.matchState$.next(info.res.game_info.matchStarted);
      }
    });

    // Reacts to the start of a Match
    overwolfEvents.onInfoUpdates2
      .addListener((infoUpdateChange: Update) => {

        if (infoUpdateChange.feature === Feature.matchState) {
          console.log(infoUpdateChange.feature);
          this.matchState$.next(true);
          this.updateSummoner();
        }
      });

    // The onInfoUpdates doesn't fire a not normal 'end of match'
    // The onNewEvents fires the 'match start' to late in the game

    // Reacts to the end of a match
    overwolfEvents.onNewEvents
      .addListener((newEvents) => {

        newEvents.events.forEach((event) => {
          if (event.name === 'matchEnd') {
            this.matchState$.next(false);
          }
        });
      });

    // Listens to the Hotkey registered in the manifest.json
    overwolf.settings.registerHotKey(
      'showWindow',
      (arg) => this.showWindow(arg)
    );
  }

  /**
   *
   * Sets the summoner Id with this Id we find the Match the player is in
   *
   */
  private updateSummoner(): void {
    overwolfEvents.getInfo((info) => {
      if (info) {
        this.summonerId$.next(info.res.summoner_info.id);
        this.region$.next(info.res.summoner_info.region);
      }
    });
  }

  /**
   *
   *  Ask the server to start a match
   *
   * @param region: Region player is in
   * @param summonerId
   */
  private startMatch (region: string, summonerId: number) {

    this.socketService.message(SocketEvents.createMatch, {
      summonerId: summonerId,
      region: region
    } as CreationRequest);

  }

  /**
   *
   * Set the event types we react to
   *
   */
  private setFeatures(): void {

    overwolfEvents.setRequiredFeatures(this.usingFeatures, (info) => {
      if (info.status === 'error') {
        // check info.status possible values
        console.log(info.reason);
        return;
      }
    });
  }

  /**
   *
   * Hides the main window
   */
  private hideWindow() {
    overwolf.windows.hide(this.MainWindow.id, () => {
    });
  }

  /**
   *
   * Shows the main window and hides it after 2 seconds using hideWindow method
   * @param arg
   */
  private showWindow(arg): void {
    console.log('in show window')
    const hideWindow$: Subject<void> = new Subject<void>();

    if (arg.status === 'success') {
      overwolf.windows.restore(this.MainWindow.id, () => {
        // Hide window after to 2 seconds
        interval(2000)
          .pipe(
            takeUntil(hideWindow$)
          )
          .subscribe(() => {
            this.hideWindow();
            hideWindow$.next();
          });
      });
    }
  }
}
